	import SadDog from "./sad-dog.svelte";
	const foods = [{"labels":["Alcohol"],"value":"bad","id":"edabc7d2-deac-423e-a6b6-c1fd7a984a3f"},{"labels":["Apricot"],"value":"bad","id":"ffcef202-460e-4d4c-a33d-6ebae48ba696"},{"labels":["Australian Nut"],"value":"bad","id":"72f35d15-3ae7-4084-9781-39a5d17c3442"},{"labels":["Avocado"],"value":"bad","id":"5842014b-632f-4b1c-a0c6-0528633771f6"},{"labels":["Bread Dough"],"value":"bad","id":"447e9158-83af-4b1d-a470-7e8360327931"},{"labels":["Caffeine"],"value":"bad","id":"7355793c-2046-4132-8532-da7691c8f2a4"},{"labels":["Cherry"],"value":"bad","id":"531ef05a-f72f-4d47-9075-23474715b77a"},{"labels":["Chocolate"],"value":"bad","id":"e41f0ee6-649f-436d-aa82-6ca29cb9b3e7"},{"labels":["Currants"],"value":"bad","id":"2a066c93-a01b-4606-bc01-82ddc4a0a154"},{"labels":["Garlic"],"value":"bad","id":"ed07827f-5e2a-4c97-8801-93894c665139"},{"labels":["Grapes"],"value":"bad","id":"e74710c1-8cd5-453e-9c88-64b47f0ea6ea"},{"labels":["Hops"],"value":"bad","id":"32481289-4988-4fdd-a6ea-05c15fed5a16"},{"labels":["Macadamia Nuts"],"value":"bad","id":"817765c8-f9da-4b95-835b-e8787d92b3cc"},{"labels":["Moldy Food (Mycotoxins)"],"value":"bad","id":"6d8fb80b-8001-4f48-8472-952f1834bade"},{"labels":["Mushrooms"],"value":"bad","id":"1512b9a3-145f-4c51-80b6-d58b287368dc"},{"labels":["Onions"],"value":"bad","id":"0e7017d9-ea50-4179-aeca-d029a567649b"},{"labels":["Peach Pits"],"value":"bad","id":"7c995944-5db2-43df-bcb2-fe545fdcc0bd"},{"labels":["Play Dough (Homemade)"],"value":"bad","id":"095e9e26-5963-4f87-8995-3b233a480a11"},{"labels":["Potato (Green)"],"value":"bad","id":"f55def40-f64d-44fe-ada3-f18650a539a3"},{"labels":["Raisins"],"value":"bad","id":"b963e94a-aca6-469e-b6eb-bf2fe0c5f39b"},{"labels":["Salt"],"value":"bad","id":"ecb4b9fd-b6bc-49a8-82f7-c5f71db90db7"},{"labels":["Star Fruit"],"value":"bad","id":"602f4eff-efc3-4eff-8210-42b93ee48dea"},{"labels":["Xylitol"],"value":"bad","id":"b41dd2a4-8643-4e3a-9e61-bfd9f3133e10"},{"labels":["Bread"],"value":"good","id":"833967cb-6850-4b09-b99a