d","id":"cb847681-1638-4720-90a0-039bec1fe954"},{"labels":["Dairy products"],"value":"avoid","id":"ce729394-13a3-406b-a212-5143e9b02418"},{"labels":["Chicken"],"value":"good","id":"b96abe26-78ce-44c3-b811-10d8372d1be4"},{"labels":["Blueberries"],"value":"good","id":"06806129-d584-4de0-8972-3a63d192dbc5"},{"labels":["Bananas"],"value":"good","id":"5f63a7af-99f4-41c8-bf22-6db07e5849f5"},{"labels":["Cucumbers"],"value":"good","id":"b99ece40-3674-4206-9823-819f7c18de13"},{"labels":["Green beans"],"value":"good","id":"a3a3c564-8e13-4740-8467-4838876c1c45"},{"labels":["Watermelon"],"value":"good","id":"5756ef89-2703-43c5-bc3c-e3e6054bd022"},{"labels":["Coffee"],"value":"bad","id":"56de71c7-592e-4c05-9158-d89af762219a"},{"labels":["Citrus"],"value":"avoid","id":"38b0f5ee-aec9-4032-af25-61aee727b4f4"},{"labels":["Nuts"],"value":"avoid","id":"db32b3d9-0057-4d78-96ec-e7b91a01e31c"},{"labels":["Dough"],"value":"bad","id":"1e4ed888-17f2-48d1-b59f-b76d15961de0"}]
	let searchInput = "";
	let selectedFood;
	
	const filterFoods = (search) => {
		if (search.length < 3) {
			return [];
		}
		const regex = new RegExp(search, "i");
		return foods.filter(d => d.labels.filter(label => label.match(regex)).length > 0);
	}
	
	const getFood = (id) => {
		return foods.find(d => d.id === id);
	}
	
	$: matchingFoods = filterFoods(searchInput);
	
	const handleClick = (id) => () => {
		searchInput = "";
		selectedFood = getFood(id);
	}
	
	const getEmotion = (food) => {
		if (!food) {
      return "waiting";
		}
		if (food.value === "good") {
      return "happy";
		}
		if (food.value === "avoid") {
      return "sad";
		}
		if (food.value === "bad") {
      return "dead";
		}
	}
	
	$: emotion = getEmotion(selectedFood);
	
	const getColor = (food) => {
		if (!food) {
      return "#A7C1F4";
		}
		if (food.value === "good") {
      return "#A2F8A2";
		}
		if (food.value === "avoid") {
      return "#FFE0A6";
		}
		if (food.value === "bad") {
      return "#FFA6A6";
		}
	}
	
	$: color = getColor(selectedFood);
	
	const colorBlack = "#0a0a0a";
</script>

<style>
