		font-size: 2em;
		text-transform: capitalize;
	}
	
	.food-value {
		font-weight: bold;
		font-size: 3em;
		text-transform: uppercase;
	}
	
	.dog {
		width: 70%;
		margin: auto;
	}
</style>

<div class="container" style="--color:{color}">
	<div class="search-container">
		<div class="search">
			<input bind:value={searchInput} placeholder="Search food..."/>
			{#each matchingFoods as food}
			<div class="search-match" on:click={handleClick(food.id)}>{food.labels.join(", ")}</div>
			{/each}
		</div>
	</div>

	{#if selectedFood}
	<div class="result">
		<div class="food-label">
			{selectedFood.labels[0]}
		</div>
		<div class="food-value">
			{selectedFood.value}
		</div>
	</div>
	{/if}

	<div class="dog-container">
		<div class="dog">
			{#if emotion === "waiting"}
			<WaitingDog stroke={colorBlack} fill={colorBlack}/>
			{/if}
			{#if emotion === "happy"}
			<HappyDog stroke={colorBlack} fill={colorBlack}/>
			{/if}
			{#if emotion === "sad"}
			<SadDog stroke={colorBlack} fill={colorBlack}/>
			{/if}
			{#if emotion === "dead"}
			<DeadDog stroke={colorBlack} fill={colorBlack}/>
			{/if}
		</div>
	</div>
</div>